import Link from "next/link";
import { cookies } from "next/headers";

export default function HomePage() {
  // Get the user role from cookies (server-side)
  const cookieStore = cookies();
  const role = cookieStore.get("user-role")?.value || "";

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-black text-white p-6">
      {/* Static welcome message */}
      <h1 className="text-4xl font-bold mb-8 animate-fade-in">
        Welcome to the Dashboard
      </h1>

      <div className="w-full max-w-2xl bg-gray-900 text-white p-10 rounded-xl shadow-2xl flex flex-col items-center space-y-6">
        {/* Show login options if user is not logged in */}
        {!role && (
          <div className="flex space-x-6">
            <Link
              href="/auth/login"
              className="flex flex-col items-center text-xl font-semibold text-black bg-white px-6 py-4 rounded-lg shadow-lg hover:scale-105 transition-transform"
            >
              Admin
            </Link>
            <Link
              href="/auth/login"
              className="flex flex-col items-center text-xl font-semibold text-black bg-white px-6 py-4 rounded-lg shadow-lg hover:scale-105 transition-transform"
            >
              Manager
            </Link>
          </div>
        )}

        {/* Show dashboard links if user is logged in */}
        {role === "admin" && (
          <Link
            href="/admin/dashboard"
            className="block text-xl font-semibold text-black bg-white px-6 py-4 rounded-lg shadow-lg hover:scale-105 transition-transform"
          >
            Go to Admin Dashboard
          </Link>
        )}
        {role === "manager" && (
          <Link
            href="/manager/dashboard"
            className="block text-xl font-semibold text-black bg-white px-6 py-4 rounded-lg shadow-lg hover:scale-105 transition-transform"
          >
            Go to Manager Dashboard
          </Link>
        )}
      </div>
    </div>
  );
}